import React from 'react';
import './recruitmentHomeItem.scss';
import moment from 'moment';
import { Box, Grid } from '@material-ui/core';
import { CircleArrowRight } from '../../../../assets/icons';

const getDay = (createAt) => {
  const day = moment(createAt).format('D');
  return day >= 10 ? day.toString() : `0${day}`;
};

const getMonth = (createAt) => {
  const month = moment(createAt).format('M');
  return `Th${month}`;
};

const RecruitmentHomeItem = (props) => {
  const { item, onClick } = props;
  return (
    <Grid
      onClick={() => {
        if (typeof onClick === 'function') {
          onClick(item);
        }
      }}
      item
      xs={12}
      sm={12}
      md={6}
      lg={6}
      xl={6}
    >
      <div className="recruitment_home_item_container">
        <div className="image-section">
          <img className="thumbnail" src={item.imageCover} />
        </div>
        <Box style={{ marginTop: '20px' }} display="flex" alignItems="center">
          <div className="created_date_box">
            <p className="created_day">{getDay(item.createdAt)}</p>
            <p className="created_month">{getMonth(item.createdAt)}</p>
          </div>
          <Box
            style={{
              marginLeft: '13px',
              height: '111px',
              flex: 1,
            }}
            display="flex"
            flexDirection="column"
            justifyContent="space-between	"
          >
            <p className="eVoucher-name">{item.name}</p>
            <Box display="flex" alignItems="center">
              <div className="eVoucher-description">Chi tiết</div>
              <CircleArrowRight style={{ marginLeft: '5px' }} />
            </Box>
          </Box>
        </Box>
      </div>
    </Grid>
  );
};

export default RecruitmentHomeItem;
