import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListHappyBucketDealHomeHandle } from 'src/redux/actions/happyBucket.action';
import { getListPromotionHomeHandle } from 'src/redux/actions/promotion.action';
import { getListRecruitmentHomeHandle } from 'src/redux/actions/recruitment.action';
import {
  getListFeatureVoucherHomeHandle,
  getListHotVoucherHomeHandle,
} from 'src/redux/actions/voucher.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import HomeView from './home.view';

const HomeContainer = () => {
  const [listHotDeal, setListHotDeal] = useState([]);
  const [listFeatureDeal, setListFeatureDeal] = useState([]);
  const [listHappyBucketDeal, setListHappyBucketDeal] = useState([]);
  const [listRecruitment, setListRecruitment] = useState([]);
  const [listPromotion, setListPromotion] = useState([]);

  const listBanner = useSelector(getListBannerSelector);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      getListHotVoucherHomeHandle((listHotDealRes) => {
        setListHotDeal(listHotDealRes);
      })
    );
    dispatch(
      getListPromotionHomeHandle((listPromotionRes) => {
        setListPromotion(listPromotionRes);
      })
    );
    dispatch(
      getListFeatureVoucherHomeHandle((listFeatureDealRes) => {
        setListFeatureDeal(listFeatureDealRes);
      })
    );
    dispatch(
      getListHappyBucketDealHomeHandle((listHappyBucketDealRes) => {
        setListHappyBucketDeal(listHappyBucketDealRes);
      })
    );
    dispatch(
      getListRecruitmentHomeHandle((listRecruitmentRes) => {
        setListRecruitment(listRecruitmentRes);
      })
    );
  }, []);

  return (
    <HomeView
      onHappyBucketItemClick={(item) => {
        window.open(item.deep_link_url, '_self');
      }}
      listRecruitment={listRecruitment}
      listHotDeal={listHotDeal}
      listFeatureDeal={listFeatureDeal}
      listHappyBucketDeal={listHappyBucketDeal}
      listBanner={listBanner}
      listPromotion={listPromotion}
    />
  );
};

export default HomeContainer;
